.container {
  margin: auto;
  margin-top: 10%;
  border: 2px solid black;
  width: 50%;
  min-height: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
  padding: 15px;
}
.box {
  border: 1px solid black;
  width: 50px;
  height: 50px;
  cursor: pointer;
}